import axios from "axios";
import auth from "@/Services/auth";

export class apiService {
  constructor() {}

  async login(data) {
    const url = `https://localhost:44362/api/Account/login`;
    let res = await axios.post(url, data);
    if (res.status === 401) {
      throw "Your username and/or password is invalid";
    }
    return res.data;
  }

  async register(data) {
    const url = `https://localhost:44362/api/Account/register`;
    let res = axios.post(url, data);
    if (res.status === 400) {
      throw res.data.message;
    }
    return res.data;
  }

  async getUserInfo(username) {
    const url = `https://localhost:44362/api/Account/${username}`;
    let res = axios.get(url);
    if (res.status === 400) {
      throw res.data.message;
    }
    return res.data;
  }

  async getMachineList() {
    const url = `https://localhost:44362/api/Workout`;
    return axios
      .get(url, {
        headers: {
          Authorization: "Bearer " + auth.getToken()
        }
      })
      .then(response => response.data);
  }

  async getMachine(id) {
    const url = `https://localhost:44362/api/Workout/${id}`;
    return axios
      .get(url, {
        headers: {
          Authorization: "Bearer " + auth.getToken()
        }
      })
      .then(response => response.data);
  }

  async addWorkout(data) {
    const url = `https://localhost:44362/api/History`;
    let res = axios.post(url, data, {
      headers: {
        Authorization: "Bearer " + auth.getToken()
      }
    });
    if (res.status === 400) {
      throw res.data.message;
    }
    return res.data;
  }
}
