<template>
<div>
      <h2>Profile</h2>
      <!--view profile-->
      <b-container v-if="editFields" >
        <b-row>
          <b-col cols="3">
          <img id="user_img" :src="user.ImageField" alt="user_image" />
          </b-col>
          <b-col cols="9">
            <h3>{{user.FirstName}} {{user.LastName}}</h3>
            <p>{{user.Username}} </p>
            <p>{{user.Email}} </p>
              {{user.visitsPerWeek}} {{user.Goals}} {{user.Level}} 

              
          </b-col>
        </b-row>
        <b-row>
          <b-col class="center_btn">
            <b-button v-on:click="editProfile">Edit Profile</b-button>
          </b-col>
        </b-row>
      </b-container>

<!--edit profile-->
  <div class="form_container" v-else>
    <b-form @submit.prevent="updateProfile">
      <div class="col_3">
          <img id="user_img" :src="user.ImageField" alt="user_image" />
      </div>
      <div class="col_9">
          <b-form-group label="First Name:" label-for="input-1">       
            <b-form-input
              id="input-1"
              v-model="user.FirstName"
              required
              size="sm"
              :value="user.FirstName">
            </b-form-input>
          </b-form-group>

          <b-form-group label="Last Name:" label-for="input-2">
            <b-form-input
              id="input-2"
              v-model="user.LastName"
              required
              size="sm"
              :value="user.LastName">
            </b-form-input>
          </b-form-group>

          <b-form-group label="Email:" label-for="input-3">
            <b-form-input
              id="input-3"
              v-model="user.Email"
              type="email"
              required
              size="sm"
              :value="user.email">
            </b-form-input>
          </b-form-group>

          <b-form-group label="Username:" label-for="input-4">
            <b-form-input
              id="input-4"
              v-model="user.Username"
              required
              size="sm"
              :value="user.Username">
            </b-form-input>
          </b-form-group>   
          
          <b-form-group label="Password:" label-for="input-5">
            <b-form-input
              id="input-5"
              v-model="user.Password"
              required
              type="password"
              size="sm"
              placeholder="Enter password">
            </b-form-input>
          </b-form-group>

          <b-form-group label="Confirm Password:" label-for="input-6">
            <b-form-input
              id="input-6"
              v-model="user.ConfirmPassword"
              required
              type="password"
              size="sm"
              placeholder="Enter password">
            </b-form-input>
          </b-form-group>

          <b-form-group label="Image URL:" label-for="input-6">
            <b-form-input
              id="input-6"
              v-model="user.ImageField"
              required
              size="sm">
            </b-form-input>
          </b-form-group>

        <b-button v-on:click="cancelEdit" >Cancel</b-button>
        <b-button type="submit">Update</b-button>
      </div>
    </b-form>
  </div><!--end of form_container-->
       
</div>
</template>

<script>
import { apiService } from "@/Services/api.js";
const apiSvc = new apiService();
import auth from "@/Services/auth.js";

export default {
  data(){
    return{
      user: [],
      editFields: true,
      Update: {
        FirstName: '',
        LastName: '',
        Email: '',
        Username: '',
        Password: '',
        ConfirmPassword: ''
      }
    }
  },
  created(){
    fetch(`${process.env.VUE_APP_REMOTE_API}/profile`, {
          method: "GET",
          headers: {
            // A Header with our authentication token.
            Authorization: "Bearer " + auth.getToken()
          }
      }).then(response => response.json())
        .then(json => {
          this.products = json;
      }),
    apiSvc.getUserProfile()
      .then(data => {
        this.user = data;
      })
      .catch(error => {
        window.console.log("Error:", error);
      }) 
  },
  methods: {
    cancelEdit(){
      this.editFields = true;
    },
    editProfile(){
      this.editFields = false;
    },
    viewEditedProfile() {
          this.$router.push("/profile");
    },
    updateProfile(){
      this.$emit('submit-registration', this.form)
    }
  }
}

</script>

