<template>
  <div>
      <div class='weight_container'>             
        <router-link 
            v-for="machine in MachineList" 
            :key="machine.id" 
            :to="{ name: 'machine', params: {machine: machine.id} }" 
            class="tile"> 
            {{machine.name}} 
        </router-link>        
      </div>
  </div>
</template>

<script>
import { apiService } from "@/Services/api";
const apiSvc = new apiService();

export default {
    data(){
        return{
            error: '',
            MachineList : [],          
        }
    },    
    created(){
        apiSvc.getMachineList()
        .then(data => {
            this.MachineList = data;
        })
        .catch (error => {
            this.error = error.message;
        })       
    }
} 
</script>

