<template>
   <div class="form_container">
     <div class="center_form">
        <b-form @submit.prevent="introRegister($event)">
          <b-form-group label="First Name:" label-for="input-1">       
            <b-form-input
              id="input-1"
              v-model="Register.FirstName"
              :class="{error: Errors.firstName}"
              v-on:keyup="removeError($event)"
              size="sm"
              placeholder="Enter first name">
            </b-form-input>
            <errors 
             :type="'firstName'"
              v-if="Errors.firstName" 
              class="error-message"
              />
          </b-form-group>

          <b-form-group label="Last Name:" label-for="input-2">
            <b-form-input
              id="input-2"
              v-model="Register.LastName"
              :class="{error: Errors.lastName}"
              v-on:keyup="removeError($event)"
              size="sm"
              placeholder="Enter last name">
            </b-form-input>
            <errors 
             :type="'lastName'"
              v-if="Errors.lastName" 
              class="error-message"
              />
          </b-form-group>

          <b-form-group label="Your Email:" label-for="input-3">
            <b-form-input
              id="input-3"
              v-model="Register.Email"
              type="email"
              :class="{error: Errors.email}"
              v-on:keyup="removeError($event)"
              size="sm"
              placeholder="Enter email">
            </b-form-input>
            <errors 
             :type="'email'"
              v-if="Errors.email" 
              class="error-message"
              />
          </b-form-group>

          <b-form-group label="Your Username:" label-for="input-4">
            <b-form-input
              id="input-4"
              v-model="Register.UserName"
              :class="{error: Errors.userName}"
              v-on:keyup="removeError($event)"
              size="sm"
              placeholder="Enter username">
            </b-form-input>
            <errors 
             :type="'userName'"
              v-if="Errors.userName" 
              class="error-message"
              />
          </b-form-group>

          <b-form-group label="Your Password:" label-for="input-5">
            <b-form-input
              id="input-5"
              v-model="Register.Password"
              :class="{error: Errors.password}"
              v-on:keyup="removeError($event)"
              type="password"
              size="sm"
              placeholder="Enter password">
            </b-form-input>
            <errors 
             :type="'password'"
              v-if="Errors.password" 
              class="error-message"
              />
          </b-form-group>

          <b-form-group label="Confirm Password:" label-for="input-6">
            <b-form-input
              id="input-6"
              v-model="Register.ConfirmPassword"
              :class="{ error: Errors.confirmPassword }"
              v-on:keyup="removeError($event)"
              type="password"
              size="sm"
              placeholder="Re-enter password">
            </b-form-input>
            <errors 
             :type="'confirmPassword'"
              v-if="Errors.confirmPassword" 
              class="error-message"
              />
          </b-form-group>

      <b-button class="next_btn" type="submit">Next</b-button>
    </b-form>
     </div>
    </div>
</template>

<script>

import Errors from '../../components/validation/Errors'
import { validator } from '../../Services/validator'

const validation = new validator();

export default {
  data() {
    return {
      Register: {
        FirstName: null,
        LastName: null,
        Email: null,
        UserName: null,
        Password: null,
        ConfirmPassword: null
      },
      Errors: {
        firstName: false,
        lastName: false,
        email: false,
        userName: false,
        password: false,
        confirmPassword: false
      }
    }
  },
  components: {
    Errors
  },
  methods: {
      introRegister(event){ 
        const validate = validation.testData(this.Register);
        this.mapErrors(validate);
        if(validate.passes === true){    
           this.$emit('intro-register', this.Register);
        } else{
           event.preventDefault();
        }
      },
      removeError(event){
        const target = event.target.id;
        const input = document.getElementById(target);
        input.classList.remove('error');
      },
      mapErrors(errorList){
        const errors = this.Errors;
        errors.firstName = errorList.firstName;
        errors.lastName = errorList.lastName;
        errors.email = errorList.email;
        errors.userName = errorList.userName;
        errors.password = errorList.password;
        errors.confirmPassword = errorList.confirmPassword;
      }
  }
}


</script>

<style scoped>
.form_container {
  width: 100%;
  margin-bottom: 100px;
}
.center_form{
  width:30%;
  margin: auto;
}
.next_btn{
  width: 47%;
}
.error{  
  background: rgb(251, 201, 201);
}
.error::placeholder{
  color: red;
}
/*mobile size */
@media screen and (max-width: 600px) {
  .form_container {
    width: 100%;
  }
  .center_form{
    width:75%;
    margin: auto;
  }
}
