<template>
  <div> 
      <h1 id="machine-title"> {{machine.name}}</h1>

      <form @submit.prevent="doThis" >  

          <div id="form-boxes">  
              <div class="grid">
                <label label-for="input-1"> 
                    Reps: 
                </label> 
                <b-form-input 
                    id="input-1"
                    v-model="sessionInfo.reps" 
                    required/>
              </div>
              <div class="grid">
                <label label-for="input-1"> 
                    Weight: 
                </label> 
                <b-form-input 
                    id="input-2"
                    v-model="sessionInfo.weight" 
                    required/>
              </div>
              <div class="grid">
                <label label-for="input-3"> 
                    Duration: 
                </label> 
                <b-form-input 
                    id="input-3"
                    v-model="sessionInfo.duration" 
                    required/>
              </div>
          </div>

          <div id="buttons">
            <b-button 
                variant="secondary" 
                id="go-back" 
                v-on:click="goBack"> 
                Go Back 
            </b-button>
            <b-button 
                variant="primary" 
                id="add-workout" 
                v-on:click="addWorkout">
                Add Workout 
            </b-button>
          </div>
      </form>
  </div>
</template>

<script>
import { apiService } from "@/Services/api";
const apiSvc = new apiService();

export default {
    props: {
        machine: Object
    },
    data(){
        return{
            dataSent:  false,
            errorSending: false,
            sessionInfo: {
                equipmentId: null,
                reps: null,
                weight: null,
                duration: null
            }
        }
    },
    methods: {
        addWorkout(){
            this.sessionInfo.equipmentId = this.machine.id;
            apiSvc.addWorkout(this.sessionInfo)
            .then( response => {
                console.log(response);
            })
        },
        goBack() {
            this.$router.go(-1);
        }
    }
}
</script>

